* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

.to-top {
  width: 50px;
  height: 50px;
  color: #1f1f1f;
  opacity: 0;
  pointer-events: none;
  background-color: #f0932b;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  text-decoration: none;
  transition: all .4s;
  display: flex;
  position: fixed;
  bottom: 16px;
  right: 32px;
}

.to-top.active {
  pointer-events: auto;
  opacity: 1;
  bottom: 32px;
}

